$browser-prefix: gutenify--template-kit-browser--;

.#{$browser-prefix}no-theme {
	padding: 50px;
	text-align: center;
	.dashicons-warning {
		color: #ff9800;
		font-size: 50px;
		height: 50px;
		width: 50px;
	}
}

.#{$browser-prefix}header-left {
	align-items: center;
	display: flex;
	gap: 30px;
	justify-content: center;
}

.gutneify-kit-type-label {
	color: #fff;
    padding: 14px;
    position: absolute;
    left: -32px;
    top: -32px;
    transform: rotate(358deg);
    z-index: 1;
    background: #fc5246;
    padding: 17px 10px !important;
    border-radius: 100%;
    width: 34px;
    height: 18px;
    box-shadow: 0 0 16px #00000036;
	&.gutneify-kit-type-label-pro {
		background-color: #4caf50;
	}
	.gutenify-kit-title & {
			border-radius: 20px;
			font-size: 14px;
			margin-left: 10px;
			padding: 5px 15px!important;
			position: relative;
			right: unset;
			/* top: -3px; */
			border-radius: 40px;
			background: orange;
			font-weight: normal;
		}
}

.gutenify-kit-title .gutneify-kit-type-label.gutneify-kit-type-label-free {
	background: #008000;
}

div.components-modal__screen-overlay div.components-card__header {
	background: #ffffff;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    padding: 5px 30px;
    box-shadow: 0 15px 21px #0000000a;
    border: none;
}
.gutneify-kit-import-toolbar {
	display: flex;
	justify-content: end;
	padding: 15px 0;
}

.gutenify-icon-color-warning {
	color: #ff9800;
}

#gutenify-kit-upload-wrap {
	display: inline;
	margin-left: 10px;
}

.page-title-action {
	display: none;
}

.gutenify-component-modal-hide-header
.components-modal__content > .components-modal__header:first-of-type {
	display: none;
}

.gutenify-plugin-templates-modal {
    max-width: 80%;
    height: auto;
    max-height: 80%;
    width: 100%;
    border-radius: 0;
}

.gutenify-component-modal-alert {
	min-width: 1050px;
	transition: all 0.5s linear;
	&.gutenify-component-modal-alert-auto-width {
		min-width: unset;
	}
	.components-card__body {
		display: flex;
		justify-content: space-between;
	}
}

.gutenify-component-modal-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	width: 100%;
}

.gutenify-component__help {
	color: rgb(117, 117, 117);
	font-size: 12px;
	font-style: normal;
}

$prefix: gutenify-template-kit-browser;
.components-surface.components-card.gutenify--template-kit-browser--header-section {
	margin: 20px 20px 0;
	box-shadow: none;
}
button.components-button.is-secondary,
.components-button.is-primary,
button.components-button.is-secondary:hover,
.components-button.is-primary:hover,
button.components-button.is-secondary:focus,
.components-button.is-primary:focus,
button.components-button.is-secondary:active,
.components-button.is-primary:active {
    background: #4caf50;
    box-shadow: inherit;
    color: #fff;
}
.gutenify-template-browser-button-group a.components-button.is-secondary {
    color: #fff;
    box-shadow: none;
    background: #3903f8;
}
.components-button.is-secondary:hover:not(:disabled), .components-button.is-tertiary:hover:not(:disabled),
.components-button.is-primary:hover:not(:disabled) {
	box-shadow: none;
	color:#fff;
	background: #f55347;
}

.#{$prefix}-wrapper {
	display: grid;
	// &:not(.#{$prefix}-has-kit-details) {
	// 	grid-template-columns: 20% 80%;
	// }
	.#{$prefix}-filter-sidebar {
		background-color: #f1f1f1;
		padding: 15px;
	}
	.#{$prefix}-toolbar {
		// display: flex;
		// justify-content: space-between;
		margin-bottom: 15px;
		// background: #f5f5f5;
		// padding: 15px;
	}

	.#{$prefix}-listing-area {
		// padding: 15px;
		> div {
			border: none;
			box-shadow: none;
			background: inherit;
		}
		margin-top: 20px;
		.#{$prefix}-lists {
			display: grid;
			grid-gap: 30px;
			grid-template-columns: 1fr 1fr 1fr 1fr;
			grid-template-rows: 1fr;
			max-width: 100%;
			> .gutenify-plugin-templates-list-item {
				left: inherit !important;
				position: inherit !important;
				top: inherit !important;

				@media screen and (max-width: 840px) {
					flex: 0 0 50%;
					width: 50%;
				}
				@media screen and (max-width: 450px) {
					flex: 0 0 100%;
					width: 100%;
				}

				> button {
				    background: none;
					background-color: #fff;
					border: none;
					box-shadow: 0 0 32px 0 #7a7a7a29;
					cursor: pointer;
					display: block;
					padding: 20px;
					transition: 0.2s border-color, 0.2s box-shadow;
					width: 100%;

					&:hover,
					&:focus,
					&:active {
						box-shadow: 1px 4px 20px -2px #0003;
					}

					.gutenify-plugin-templates-list-item-image {
						display: block;
						position: relative;
						img {
							display: block;
							height: auto;
							width: 100%;

						}

						.gutenify-plugin-templates-list-item-image-sizer {
							width: 100%;

							+ img,
							+ .lazyload-wrapper img {
								height: 100%;
								left: 0;
								position: absolute;
								top: 0;
							}
						}
					}
					span {
						display: inline-block;
						padding: 5px 40px;
					}
					> span {
						background: #000;
						color: #fff;
						display: block;
						font-size: 17px;
						padding: 10px 30px;
					}
				}

				.gutenify-plugin-templates-list-item-title {
					font-size: 10px;
					font-weight: 600;
					padding: 10px;
					text-transform: uppercase;
					// border-top: 1px solid $light-gray-100;
				}

				&.gutenify-plugin-templates-list-item-no-thumb > button {
					padding: 20px 10px;
					text-align: center;
					// background-color: $light-gray-200;
					// border: 1px solid $light-gray-300;
				}
			}
		}
	}
	&.#{$prefix}-loading {
		position: relative;
		&::before {
			background-color: #f7f7f752;
			content: "";
			height: 100%;
			position: absolute;
			width: 100%;
			z-index: 1;
		}
	}
}

@keyframes fadeInAnimation {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}
@keyframes fadeInAnimation2 {
	0% {
		left: -20px;
		opacity: 0;
	}

	100% {
		left: 0;
		opacity: 1;
	}
}

.gutenify-component-modal-success-message {
	text-align: center;
	.gutenify-component-modal-success-message-icon {
		animation: fadeInAnimation ease 1s;
		animation-fill-mode: forwards;
		animation-iteration-count: 1;
		color: #4caf50;
		opacity: 0;
		position: relative;
		transition: all 1s ease-in-out;
	}
}

.gutenify-kit-imported-list {
	color: #4caf50;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 1fr 1fr;
	margin: 0 auto;
	margin: 50px auto;
	max-width: 400px;
	text-align: left;
	li {
		animation: fadeInAnimation2 ease 1s;
		animation-fill-mode: forwards;
		animation-iteration-count: 1;
		opacity: 0;
		position: relative;
		transition: all 1s ease-in-out;

		@for $i from 2 through 5 {
			&:nth-child(#{$i}n) {
				animation-delay: #{$i * 0.5}s;
			}
		}
	}
}

.gutenify-template-kit-information-wrapper {
    display: grid;
    grid-template-columns: 70% 30%;
    gap: 30px;
    justify-content: center;
    padding: 30px;
	p{
		margin:0 0 30px;
	}
	.gutenify-template-kit-information-content {
		img {
		    height: auto;
			max-width: 100%;
			width: 100%;
		}
	}
	.gutenify-template-kit-information-sidebar {
		background-color: #ffffff;
		border-radius: 0;
		padding: 25px 45px;
		box-shadow: 0 6px 30px 7px #2125471a;
	}
}

.gutenify-template-browser-button-group {
	display: flex;
	gap: 15px;
}

.gutenify-template-kit-information-content ul li {
	list-style-type: disc;
	margin-left: 20px;
}

.components-card.gutenify-template-kit-browser-pro-notice{
	background-color: #4caf4f;
	color: #fff;
	margin: 20px 20px 0;
	box-shadow: none;
	border-radius: 5px;
	a{
		color: #fff;
	}
}
